* {
  margin: 0;
  padding: 0;
}

.app {
 
  text-align: center;
  display: flex;

}

.game {
  margin-left: 500px;
  margin-top: 55px;
  width: 560px;
  height: 560px;
  display: flex;
  flex-wrap: wrap;
  background-color: rgba(250, 250, 250, .2);
  border: 20px solid black;
}

.game img {
  width: 70px;
  height: 70px;
}

#bg-video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}

video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
  }

  .score {
  color: white;
  padding-left: 250px;
  font-size: 80px;
  font-family: 'Press Start 2P';
  margin-top: 30px
  }


 

  .timer {
    position: absolute;
    margin-top: 200px;
    margin-left: 150px;
  }

  #counter-button {
    color: white;
    width: 200px;
    background-color: transparent;
    border: 1px solid rgba(104, 234, 52, 0.3);
  }

  #counter-button:hover {
    background-color: rgba(104, 234, 52, 0.1);
    
  }

  #countdown {
    color: white;
    font-size: 80px;
  }
  
  .time-header {
    color: white;
    font-size: 25px;
  }

  .score-container {
    margin-top: 60px
  }

